export default class GRAXCloudConfigurationForm extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoading: true,
      isTesting: false,
      error: null,
      configuration: {},
      metadataBackupIndex: 0
    };

    this.triggerConfigurationChange = this.triggerConfigurationChange.bind(this);
  }

  componentDidMount() {
    this.setState({
      title: this.props.title,
      configuration: _.cloneDeep(this.props.configuration),
      isLoading: false
    });
  }

  triggerConfigurationChange() {
    if (this.props.onChange) {
      this.props.onChange(this.state.title, this.state.configuration);
    }
  }

  testElasticHandler = () => {
    const configuration = this.state.configuration;

    /*if (
      configuration == null ||
      configuration.elastic == null ||
      configuration.elastic.length === 0
    ) {
      alert("Incomplete data");
    }*/

    this.setState({
      isTesting: true
    });

    $.ajax({
      type: "POST",
      url: baseUrl + "/graxproxy/api/elastichealthcheck",
      contentType: "application/json; charset=utf-8",
      data: JSON.stringify({
        elasticEnvConfig: configuration.elastic[0] || {}
      }),
      skipFail: true,
      success: function(res) {
        console.log(res);

        this.setState({
          isTesting: false
        });

        alert(res.data.ElasticSearch);
      }.bind(this),
      error: function(xhr, status, err) {
        console.error(xhr, status, err);

        this.setState({
          isTesting: false
        });

        alert("Error!");
      }.bind(this)
    });
  };

  testStorageHandler = () => {
    const configuration = this.state.configuration;

    if (
      configuration == null ||
      configuration.storage == null ||
      configuration.storage.length === 0
    ) {
      alert("Incomplete data");
    }

    this.setState({
      isTesting: true
    });

    $.ajax({
      type: "POST",
      url: baseUrl + "/graxproxy/api/storageproviderhealthcheck",
      contentType: "application/json; charset=utf-8",
      data: JSON.stringify({
        storageEnvConfig: configuration.storage[0] || {}
      }),
      skipFail: true,
      success: function(res) {
        console.log(res);

        this.setState({
          isTesting: false
        });

        alert(res.data.aws); // TODO: check Azure storage
      }.bind(this),
      error: function(xhr, status, err) {
        console.error(xhr, status, err);

        this.setState({
          isTesting: false
        });

        alert("Error! status: " + status);
      }.bind(this)
    });
  };

  onNameChangeHandler = e => {
    e.persist();

    this.setState(
      {
        title: e.target.value
      },
      () => {
        this.triggerConfigurationChange();
      }
    );
  };

  onElasticProviderChangeHander = e => {
    e.persist();

    const configuration = this.state.configuration;

    _.set(configuration, "elastic[0]", {
      provider: e.target.value
    });

    this.setState({
      configuration
    });

    this.triggerConfigurationChange();
  };

  onStorageProviderChangeHandler = e => {
    e.persist();

    const configuration = this.state.configuration;

    const currentValue = _.get(configuration, "storage[0].provider", "");
    const newValue = e.target.value;

    if (currentValue === "aws" && newValue === "aws-ecs") {
      _.set(configuration, "storage[0].provider", newValue);
    } else {
      _.set(configuration, "storage[0]", {
        provider: newValue
      });
    }

    this.setState({
      configuration
    });

    this.triggerConfigurationChange();
  };

  onElasticUrlChangeHandler = e => {
    e.persist();

    const configuration = this.state.configuration;

    _.set(configuration, "elastic[0].url", e.target.value);

    this.setState({
      configuration
    });

    this.triggerConfigurationChange();
  };

  onGetConfig = type => {
    return (key, defaultValue = null) => {
      return _.get(this.state.configuration, [type, "0", key], defaultValue);
    };
  };

  onChangeConfig = type => {
    return (f, targetKey = "value") => {
      return e => {
        if (e != null && e.hasOwnProperty("originalEvent")) e.persist();

        const configuration = this.state.configuration;

        _.set(configuration, [type, "0", f], e.target[targetKey]);

        this.setState({
          configuration
        });

        this.triggerConfigurationChange();
      };
    };
  };

  onSourceControlChangeHandler = sourceControlEnabled => {
    this.onChangeConfig("metadataBackup")("sourceControlEnabled", "checked")({
      target: { checked: sourceControlEnabled }
    });

    const sourceControlProvider = this.onGetConfig("metadataBackup")("sourceControlProvider");

    if (sourceControlEnabled && !sourceControlProvider) {
      this.onChangeConfig("metadataBackup")("sourceControlProvider")({
        target: { value: "github" }
      });
    }
  };

  render() {
    if (this.state.isLoading) {
      return <Spinner size="large" variant="brand" />;
    }

    return (
      <React.Fragment>
        {this.state.isTesting ? <Spinner size="large" variant="brand" /> : null}

        <div className="slds-p-around_medium">
          <SLDSSection title="General Information">
            <div className="slds-grid slds-gutters_direct slds-grid_vertical-align-center">
              <div className="slds-col slds-size_1-of-2">
                <Input
                  id="configuration-title"
                  label="Configuration Title"
                  defaultValue={this.state.title}
                  onBlur={this.onNameChangeHandler}
                />
              </div>
            </div>

            <div className="slds-grid slds-gutters_direct slds-grid_vertical-align-center slds-p-top_medium">
              <div className="slds-col">
                <div className="slds-form-element">
                  <label className="slds-form-element__label" htmlFor="elastic-search-provider">
                    Elastic Search Provider
                  </label>
                  <div className="slds-form-element__control">
                    <div className="slds-select_container">
                      <select
                        className="slds-select"
                        id="elastic-search-provider"
                        value={this.onGetConfig("elastic")("provider", null)}
                        onChange={e => this.onElasticProviderChangeHander(e)}
                      >
                        <option value="" disabled hidden selected>
                          -- Select --
                        </option>
                        <option value="bonsai">Bonsai Elastic Search</option>
                        <option value="aws">AWS Elastic Search</option>
                        <option value="elastic.co">Elastic.co</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>

              <div className="slds-col">
                <div className="slds-form-element">
                  <label className="slds-form-element__label" htmlFor="cloud-storage-provider">
                    Cloud Storage Provider
                  </label>
                  <div className="slds-form-element__control">
                    <div className="slds-select_container">
                      <select
                        className="slds-select"
                        id="cloud-storage-provider"
                        value={this.onGetConfig("storage")("provider", null)}
                        onChange={this.onStorageProviderChangeHandler}
                      >
                        <option value="" disabled hidden selected>
                          -- Select --
                        </option>
                        <option value="aws">AWS S3 Storage</option>
                        <option value="aws-ecs">AWS ECS</option>
                        <option value="azure">Azure BLOB Storage</option>
                        <option value="google">Google Cloud Provider</option>
                        <option value="minio">MinIO</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </SLDSSection>

          {_.get(this.state.configuration, "elastic[0].provider") === "bonsai" ? (
            <BonsaiElasticProvider
              onChangeConfig={this.onChangeConfig("elastic")}
              onGetConfig={this.onGetConfig("elastic")}
              onTest={this.testElasticHandler}
            />
          ) : null}

          {_.get(this.state.configuration, "elastic[0].provider") === "aws" ? (
            <AWSElasticProvider
              onChangeConfig={this.onChangeConfig("elastic")}
              onGetConfig={this.onGetConfig("elastic")}
              onTest={this.testElasticHandler}
            />
          ) : null}

          {_.get(this.state.configuration, "elastic[0].provider") === "elastic.co" ? (
            <ElasticCoProvider
              onChangeConfig={this.onChangeConfig("elastic")}
              onGetConfig={this.onGetConfig("elastic")}
              onTest={this.testElasticHandler}
            />
          ) : null}

          {_.get(this.state.configuration, "storage[0].provider") === "azure" ? (
            <AzureStorageProvider
              onChangeConfig={this.onChangeConfig("storage")}
              onGetConfig={this.onGetConfig("storage")}
              onTest={this.testStorageHandler}
            />
          ) : null}

          {_.get(this.state.configuration, "storage[0].provider", "").startsWith("aws") ? (
            <AWSStorageProvider
              onChangeConfig={this.onChangeConfig("storage")}
              onGetConfig={this.onGetConfig("storage")}
              onTest={this.testStorageHandler}
            />
          ) : null}

          {_.get(this.state.configuration, "storage[0].provider") === "google" ? (
            <GoogleStorageProvider
              onChangeConfig={this.onChangeConfig("storage")}
              onGetConfig={this.onGetConfig("storage")}
              onTest={this.testStorageHandler}
            />
          ) : null}

          {_.get(this.state.configuration, "storage[0].provider") === "minio" ? (
            <MinioStorageProvider
              onChangeConfig={this.onChangeConfig("storage")}
              onGetConfig={this.onGetConfig("storage")}
              onTest={this.testStorageHandler}
            />
          ) : null}

          {/*<SLDSSection title="Marketing Cloud" className="slds-p-top_large">
                        <div className="slds-grid slds-gutters_direct slds-grid_vertical-align-start">
                            <div className="slds-col">
                                <Input id="marketing-cloud-sftp-username" label="SFTP Username" />
                                <Input id="marketing-cloud-sftp-password" label="SFTP Password" />
                            </div>

                            <div className="slds-col">
                                <Input id="marketing-cloud-sftp-host" label="SFTP Host" />
                            </div>
                        </div>

                        <div className="slds-p-top_medium">
                            <Button label="Test" variant="brand" />
                        </div>
                    </SLDSSection>*/}

          <SLDSSection title="Metadata Backup" className="slds-p-top_large">
            <GRAXEnvSetting
              id="SALESFORCE_GIT_INTEGRATION"
              setting={{
                name: "SALESFORCE_GIT_INTEGRATION",
                type: "boolean",
                id: "sourceControlEnabled",
                helpText: 'Set to "true" to backup SF metadata'
              }}
              defaultValue={this.onGetConfig("metadataBackup")("sourceControlEnabled")}
              onChange={this.onSourceControlChangeHandler}
            />

            {this.onGetConfig("metadataBackup")("sourceControlEnabled") ? (
              <React.Fragment>
                <RadioButtonGroup
                  labels={{
                    label: "Provider"
                  }}
                  onChange={this.onChangeConfig("metadataBackup")("sourceControlProvider")}
                  className="slds-m-top_small"
                >
                  <Radio
                    id="metadata-backup-sourceControlProvider-github"
                    label="Github"
                    value="github"
                    checked={
                      this.onGetConfig("metadataBackup")("sourceControlProvider") === "github"
                    }
                    variant="button-group"
                  />

                  <Radio
                    id="metadata-backup-sourceControlProvider-gitlab"
                    label="GitLab"
                    value="gitlab"
                    checked={
                      this.onGetConfig("metadataBackup")("sourceControlProvider") === "gitlab"
                    }
                    variant="button-group"
                  />
                </RadioButtonGroup>

                {_.map(
                  METADATA_BACKUP_SETTINGS_BY_PROVIDER[
                    this.onGetConfig("metadataBackup")("sourceControlProvider")
                  ],
                  (setting, i) => {
                    const key = "grax-env-setting-" + setting.id;

                    return (
                      <GRAXEnvSetting
                        id={key}
                        key={key}
                        setting={setting}
                        defaultValue={this.onGetConfig("metadataBackup")(setting.id)}
                        onChange={value =>
                          this.onChangeConfig("metadataBackup")(setting.id)({ target: { value } })
                        }
                      />
                    );
                  }
                )}
              </React.Fragment>
            ) : null}
          </SLDSSection>

          <SLDSSection title="Sync Analytics & Marketing Cloud" className="slds-p-top_large">
            {_.map(SYNC_ANALYTICS_AND_MARKETING_CLOUD_SETTINGS, (setting, i) => {
              const key = "grax-env-setting-" + (setting.id || setting.name);

              return (
                <GRAXEnvSetting
                  id={key}
                  key={key}
                  setting={setting}
                  defaultValue={this.onGetConfig("syncAnalytics")(setting.id)}
                  onChange={value =>
                    this.onChangeConfig("syncAnalytics")(setting.id)({ target: { value } })
                  }
                />
              );
            })}
          </SLDSSection>

          <SLDSSection title="Time Machine" className="slds-p-top_large">
            {_.map(TIME_MACHINE_SETTINGS, (setting, i) => {
              const key = "grax-env-setting-" + (setting.id || setting.name);

              return (
                <GRAXEnvSetting
                  id={key}
                  key={key}
                  setting={setting}
                  defaultValue={this.onGetConfig("timemachine")(setting.id)}
                  onChange={value =>
                    this.onChangeConfig("timemachine")(setting.id)({ target: { value } })
                  }
                />
              );
            })}
          </SLDSSection>
        </div>
      </React.Fragment>
    );
  }
}
